<template>
  <v-app>
    <board-header></board-header>
    <v-content app style="background-color:rgb(81, 152, 57); overflow: auto;" class="px-2">
      <v-layout class="py-3" align-center>
        <v-text-field v-if="titleEditing" dark ref="boardTitle" class="title font-weight-bold" autofocus hide-details box :value="boardTitle" @blur="saveTitle" @keyup.enter="saveTitle"></v-text-field>
        <div v-else class="title white--text font-weight-bold pl-2 clickable" @click="changeTitleEditing">{{boardTitle}}</div>
        <div class="body-2 font-weight-light white--text pl-2 pr-3">| Personal | Private</div>
      </v-layout>
      <v-layout class="pb-3">
        <card-list v-for="cardList in cardLists" :key="cardList.listSeq" :cardList="cardList"></card-list>
        <div>
          <v-card
            dark
            color="#3f6f21"
            width="20em"
            flat
            class="px-4 py-2 ml-1 clickable"
            @click="addCardList({listTitle:'List Title'})"
          >
            <span>+ Another another list</span>
          </v-card>
        </div>
      </v-layout>
    </v-content>
  </v-app>
</template>

<script>
import BoardHeader from "../components/board/BoardHeader";
import CardList from "../components/board/CardList";
import StoreConfig from "../store/StoreConfig";
import { mapActions } from "vuex";
import UrlConfig from '../api/UrlConfig';

export default {
  name: "board",
  components: {
    BoardHeader,
    CardList
  },
  data: function(){
    return {
      titleEditing: false
    }
  },
  computed: {
    boardTitle() {
      return this.$store.state.board.boardTitle;
    },
    cardLists() {
      return this.$store.state.board.cardLists;
    }
  },
  methods: {
    changeTitleEditing: function(){
      this.titleEditing = !this.titleEditing
    },
    saveTitle: function(e){
      this.titleEditing = false

      if(e.target.value){
        this.$store.dispatch(StoreConfig.saveBoardTitle, e.target.value)
      }
    },
    ...mapActions([StoreConfig.addCardList])
  },
  beforeCreate: function(){
    this.$axios.get(UrlConfig.board.getBoard + "/0")
    .then((response) => {
      this.$store.dispatch(StoreConfig.getBoard, response.data)
    })
    .catch((ex) => {
      alert(ex)
      // this.$router.push("/")
    })
  }
};
</script>

<style>
.clickable {
  cursor: pointer;
}
.clickable:hover {
  opacity: 0.6;
}
</style>